import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as GlobalAction from '../redux/actions/globalAction'
import {Modal,Typography,Button} from '@material-ui/core';
import Slide from '@material-ui/core/Slide';

class DialogComponent extends Component{
    render(){
        return(
            // <Slide direction="up" in={true} mountOnEnter unmountOnExit>
            <Modal style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
          }}
              open={this.props.show}
              onClose={()=>{this.props.hide()}}
              aria-labelledby="simple-modal-title"
              aria-describedby="simple-modal-description"
          >
          
              <div style={{
                 
                  width: '25%',
                  minWidth:'250px',
                  backgroundColor: 'white',
                //   border: '2px solid #15507B',
                  borderRadius:'10px',
                  padding:'20px',
                  margin:'10px',
                  textAlign:'left',
                  alignItems:'center',
                  textAlign:'center',
                  position:'relative'
              }}>
                    <div style={{
                        
                        width:'75px',
                        height:'75px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'-37px',
                        left:'50%',
                        transform:'translateX(-50%)'
                    }}>
                        <img src={`/static/assets/img/dlg/${this.props.dlgInfo.type}.png`} style={{width:'70%',height:'70%',borderRadius:'50%',marginTop:'10%'}}></img>
                    </div> 
                    <div style={{
                        
                        width:'40px',
                        height:'40px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'5px',
                        right:'15px'
                        
                    }}>
                         {/* <img src={`./static/assets/img/dlg/${this.props.dlgInfo.type}.jpg`} onClick={()=>{this.props.hide()}} style={{width:'100%',height:'100%',cursor:'hand'}}></img> */}
                    </div> 
                  
                  <Typography style={{width:'100%',color:'black',fontSize:'25px',padding:'40px 20px 10px 20px',fontWeight:'bold',opacity:'0.8'}}>
                      {this.props.dlgInfo.title}
                  </Typography>
                  <Typography style={{width:'100%',color:'black',padding:'10px 20px 20px 10px',opacity:'0.6'}}>
                  {this.props.dlgInfo.msg}
                  </Typography>
                 <Button onClick={()=>{this.props.hide()}} color="primary" style={{textTransform:'none', backgroundColor:'rgba(73, 154, 236, 1)',width:'50%',height:'40px',margin:'10px 20px 20px 20px'}} variant='contained'>OK</Button>
              </div>
              
          </Modal>
        //   </Slide>
        )
    }
}
const mapStateToProps = (state) =>({
    show:state.global.showDialog,
    dlgInfo:state.global.dlgInfo,
    
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
      
        hide :GlobalAction.hideDialog
    }, dispatch);
}


export default connect(mapStateToProps,mapDispatchToProps)(DialogComponent)